import React from "react";
import "../../src/style.css";

const styles = {
    containerStyle: {
        margin: "50px 10px 50px 10px"
    }
}

function Contact() {
    return (
        <div className="my-container" style={styles.containerStyle}>

            <div className="row">
                <div className="col-lg-12">
                    <h1>Contact Me</h1>
                    <hr />
                    <p>
                        Feel free to message me anytime! I check my emails daily, and I'll do
                        my best to respond as quickly as possible.
                    </p>
                    <form id="myForm">

                        <div className="form-group">
                            <label for="nameInp">Name</label>
                            <input type="text" className="form-control" id="nameInp" placeholder="Name" required />
                        </div>
                        <div className="form-group">
                            <label for="emailInp">Email address</label>
                            <input type="email" className="form-control" id="emailInp" placeholder="name@example.com" required />
                        </div>
                        <div className="form-group">
                            <label for="messageInp">Message</label>
                            <textarea className="form-control" id="messageInp" rows="3" required></textarea>
                        </div>
                        <button className="btn btn-dark" type="submit">Submit</button>

                        <p>
                            <br />
                            If you need to speak with me sooner, please email me directly at celineross@gmail.com, or
                            text/call at 512-968-8048.
                        </p>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact;